<template>
  <b-row>
    <b-col cols="2"></b-col>
    <b-col cols="8">
      <svg class="loader" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 340 340">
        <circle cx="170" cy="170" r="160" stroke="#A9E8DC"/>
        <circle cx="170" cy="170" r="135" stroke="#02BEC4"/>
        <circle cx="170" cy="170" r="110" stroke="#0284A8"/>
        <circle cx="170" cy="170" r="85" stroke="#050C42"/>
      </svg>
    </b-col>
  </b-row>
</template>
<script>
export default {
  name: 'loader',
  data: function () {
    return {}
  }
}
</script>
<style lang="scss" scoped>
  .loader {
    max-width: 15rem;
    width: 100%;
    height: auto;
    stroke-linecap: round;
  }

  circle {
    fill: none;
    stroke-width: 3.5;
    animation-name: preloader;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    transform-origin: 170px 170px;
    will-change: transform;

    &:nth-of-type(1) {
      stroke-dasharray: 550px;
    }

    &:nth-of-type(2) {
      stroke-dasharray: 500px;
    }

    &:nth-of-type(3) {
      stroke-dasharray: 450px;
    }

    &:nth-of-type(4) {
      stroke-dasharray: 300px;
    }

    @for $i from 1 through 4 {
      &:nth-of-type(#{$i}) {
        animation-delay: -#{$i * 0.15}s;
      }
    }
  }

  @keyframes preloader {
    50% {
      transform: rotate(360deg);
    }
  }
</style>



